import React, { useEffect } from "react";
import { Card, CardBody, CardHeader } from "react-bootstrap";
import { getFlagCode, hideLangFilter } from "../../../../../utils";
import { getLanguageTags } from "../../../../../actions/loginActions";
import { shallowEqual, useDispatch } from "react-redux";
import { GET_POST_LANGUAGE, POST_BY_LANGUAGE_LOADER } from "../../../../../redux/reducersKeys";
import AssetsbySkeleton from "../../layout/skeleton/assetsbySkeleton";
import NoData from "../../layout/common/noData";
import { useSelector } from "react-redux";

const AssetsLanguage = (props) => {
  const { postbyLanguage, getFlags, loader } = props;
  const dispatch = useDispatch()
  
  
  const { analyticwall } = useSelector(
    ({ analyticwall }) => ({
      analyticwall: analyticwall.selectedWallFeed.feeds,
    }),
    shallowEqual
  );


    const languageTags=()=>{

      const feedList = analyticwall && Object.keys(analyticwall).length > 0 ? analyticwall.map(item => item.id) : [];
      
      dispatch({ type: POST_BY_LANGUAGE_LOADER, payload: true })
      getLanguageTags(localStorage.getItem('ownerId')).then((response) => {

          let langs = response.data;
          let result = [];
          if(langs && langs.length > 0){
              langs = langs.filter((lang) => {
                  if(hideLangFilter(feedList, lang.feedId,1)) return lang;
              });
              langs.splice(6).sort((a, b) => b.count - a.count).forEach(lang => {
                  if(lang.language != ""){
                      result.push(lang.language);
                  }
              });
          }

          dispatch({ type: GET_POST_LANGUAGE, payload: langs })
       }).catch(ex => {
        dispatch({ type: POST_BY_LANGUAGE_LOADER, payload: false })
       });
      }
 

  useEffect(() => {
      languageTags()
  }, []);

  
  return (
    <Card className="sw-card-custom gutter-b card-shadow mb-5">
      <CardHeader className="p-5 d-flex align-items-center justify-content-between">
        <h6 class="card-label font-weight-bolder m-0">Top Languages</h6>
      </CardHeader>
      <CardBody className="p-5">  
        <div className="mx-height-345">
        { loader ? <AssetsbySkeleton/> : postbyLanguage && Object.keys(postbyLanguage).length
          ? postbyLanguage.map((data,index) => {
              return (
                <div className="px-5 py-2 border rounded mb-4 last-child-mb-0">
                  <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center me-10">
                      <div className="rounded me-3 d-flex">
                        <div class={`iti-flag ${getFlagCode(data.language,getFlags)}`}></div> 
                      </div>
                      <div className="">
                        <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                           { data.language }
                        </p>
                      </div>                 
                    </div>
                    <p className="count-tag border font-weight-boldest m-0">
                     { data.count }
                    </p>
                  </div>
                  </div>
                );
              })
            : <NoData/>}
        </div>
         
        </CardBody>
      </Card>
  );
};

export default React.memo(AssetsLanguage);
